@use 'base/variables' as v;
@use 'base/mixins' as m;

.profile-header{
  display: flex;
  justify-content: space-between;
  align-items: center;

  .profile-container{
    display: flex;
    gap: 1rem;
    align-items: center;
  
    .img-container{
      background-color: v.$principalColor;
      width: 5rem;
      height: 5rem;
      border: none;
      outline: none;
      border: 1rem solid transparent;
      border-radius: 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 3rem;
      color: v.$white;
    }

    .text-container{
      .link-change-password{
        font-size: 1.5rem;
      }
    }
  }

  .logout-container{
    a{
      display: flex;
      align-items: center;
      column-gap: 1rem;
      color: v.$principalColor;
      font-size: 2.5rem;
      padding: 1.5rem;
      border-radius: 1rem;
      transition: all .3s ease;
  
      &:hover{
        background-color: v.$principalColor;
        color: v.$white;
      }
    }
  }
}

.profile-content{
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 2rem;

  .user-info{
    width: 100%;
    max-width: none;
    margin: 0;
  }

  .skills-list-container{
    h2{text-align: start;}
    .skills{margin-top: 2rem;}
  }

  .add-skills-container{
    padding: 0;
    
    h2{
      margin-top: 0;
    }
    .field-group{
      margin: 0;
    }

    .required-skills{
      margin-top: 2rem;
      
      h2{
        margin-top: 3rem;
        margin-bottom: 0;
      }
    }
  }
}